.App {
   height: auto;
   width: 100vw;
   text-align: center;
   display: flex;
   flex-direction: row;
   justify-content: center;
   align-items: center;
}

@media only screen and (max-width: 900px) {
   .App {
      flex-direction: column;
      justify-content: flex-start;
   }
}
