.keyboard {
  width: 30vh;
  height: 50vh;
  margin-top: 12vh;
  display: -ms-grid;
  display: grid;
  margin-left: 1vw;
  -ms-grid-rows: 2fr 3fr 3fr 3fr 2fr 2fr;
      grid-template-rows: 2fr 3fr 3fr 3fr 2fr 2fr;
  -ms-grid-columns: 1fr 1fr 1fr;
      grid-template-columns: 1fr 1fr 1fr;
      grid-template-areas: "s s s" "1 2 3" "4 5 6" "7 8 9" "x x x" "r r r";
}

.keyboard button {
  text-shadow: none;
  outline: none;
  background: none;
  border: 1px solid black;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}

.keyboard button h4 {
  font-size: 1.5vw;
  letter-spacing: 2px;
  font-weight: normal;
  margin: 0;
}

.keyboard button:hover {
  background-color: rgba(200, 200, 200, 0.4);
  cursor: pointer;
}

.keyboard button:active {
  background-color: rgba(200, 200, 200, 0.75);
}

.keyboard .delete-button {
  -ms-grid-row: 5;
  -ms-grid-column: 1;
  -ms-grid-column-span: 3;
  grid-area: x;
  border-left: 1px solid black;
  border-bottom: 0px;
  border-top: 0px;
}

.keyboard .solve-button {
  -ms-grid-row: 1;
  -ms-grid-column: 1;
  -ms-grid-column-span: 3;
  grid-area: s;
  border-bottom: 0px;
}

.keyboard .reset-button {
  -ms-grid-row: 6;
  -ms-grid-column: 1;
  -ms-grid-column-span: 3;
  grid-area: r;
}

.animating-overlay {
  background-color: white;
  z-index: 2;
  width: 30vh;
  height: 50vh;
  margin-top: 12vh;
  margin-left: 1vw;
  position: absolute;
  border: 1px solid black;
  display: flex;
  align-items: center;
  justify-content: center;
}

.animating-overlay h2 {
  font-weight: normal;
}

.hidden {
  display: none;
}

@media only screen and (max-width: 900px) {
  .keyboard {
    margin-top: 5vh;
    margin-bottom: 2vh;
    -ms-grid-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
        grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
    -ms-grid-rows: 4fr 1fr 4fr;
        grid-template-rows: 4fr 1fr 4fr;
        grid-template-areas: "1 2 3 4 5 6 7 8 9" ". . . . . . . . ." "s s s x x x r r r";
    width: 80vw;
    height: 150px;
    margin-bottom: 10vh;
  }
  .keyboard .delete-button,
  .keyboard .solve-button,
  .keyboard .reset-button {
    border: 0px;
    flex-direction: column;
  }
  .keyboard .delete-button svg,
  .keyboard .solve-button svg,
  .keyboard .reset-button svg {
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
  }
  .animating-overlay {
    width: 80vw;
    height: 150px;
    margin-top: 5vh;
    border: none;
  }
}

@media only screen and (max-width: 1024px) {
  .keyboard button h4 {
    font-size: 2vw;
  }
  .keyboard .key h4 {
    font-size: 3vw;
  }
}

@media only screen and (max-width: 768px) {
  .keyboard button h4 {
    font-size: 3vw;
  }
  .keyboard .key h4 {
    font-size: 3.5vw;
  }
}

@media only screen and (max-width: 600px) {
  .keyboard button h4 {
    font-size: 5vw;
  }
  .keyboard .key h4 {
    font-size: 6.5vw;
  }
}
