nav {
  height: 12vh;
  width: 100vw;
  position: absolute;
  top: 0px;
  left: 0px;
  background-color: #d2e0bf;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
}

nav .title {
  letter-spacing: 3px;
  font-weight: 500;
  font-size: 1.6vw;
}

nav .tutorial-button {
  background: none;
  border: none;
  position: absolute;
  right: 5vw;
  text-shadow: none;
  outline: none;
  transition: 0.5s ease-in-out;
}

nav button:hover {
  -webkit-transform: rotate(360deg);
          transform: rotate(360deg);
}

@media only screen and (max-width: 1024px) {
  nav .title {
    font-size: 2.5vw;
  }
}

@media only screen and (max-width: 900px) {
  nav {
    height: 60px;
  }
  nav .tutorial-button {
    transition: none;
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
  }
  nav .title {
    font-size: 20px;
  }
}
